import { FileLoader } from "core/utils/fileloader_override"

/**
 * 参数说明：
 * 线程消息接受一个对象参数，包含如下字段：
 * {
 * url // 数据来源
 * }
 */
self.onmessage = function (msg) {
    var url = msg.data.url;
    var loader = new FileLoader();
    loader.setResponseType('arraybuffer');
    loader.setWithCredentials(false);
    var cur = 0;
    loader.load(url, (data, cnt) => {
        cur++;
        self.postMessage({
            type: "loaded",
            url: url,
            data_buffer: data,
        }, [data]);
        if (cur == cnt) self.postMessage({ type: "done" });
    }, xhr => {
        // ajax下载索引压缩包的进度
        self.postMessage({
            type: "loading",
            url: url, // xhr.target.responseURL,
            download_info: {
                loaded: xhr.loaded,
                total: xhr.total
            }
        });
    }, err => {
        self.postMessage({
            type: "load_error",
            url: url,
            error: {
                url: err.currentTarget.responseURL,
                status: err.currentTarget.status,
                desc: err.currentTarget.statusText
            }
        });
        self.postMessage({ type: "done" });
    });
};
